  handleChooseClick(i) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
	const current = history[history.length - 1];
    const counts = current.counts.slice();
    const prices = current.prices.slice();
    const names = current.names.slice();
    const sizes = current.sizes.slice();
    const types = current.types.slice();
    
	this.setState({
      history: history.concat([
        {
		  counts: counts,
		  prices: prices,
		  names: names,
		  sizes: sizes,
		  types: types
        }
      ]),
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext,
	  currentchoose: i
    });	  	
  }
  
